* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.template {
    width: 33.33%;
    display: inline-block;
    border: 10px solid transparent;
}

.App h1 {
    text-align: center;
    margin: 10px;
}

.template .image {
    width: 100%;
    height: 500px;
    border: 1px solid black;
    background-size: cover;
    background-position: center;
}

.meme {
    text-align: center;
}

.meme img {
    width: auto;
    height: 500px;
    border: 1px solid black;
}

.meme input {
    margin: 10px;
    padding: 5px;
}

.meme button {
    padding: 5px;
    margin: 10px;
}